.hamburger-menu {
    position: relative;
  }
  
  .hamburger-button {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    width: 2rem;
    height: 2rem;
    background: transparent;
    border: none;
    cursor: pointer;
    padding: 0;
    z-index: 10;
  }
  
  .hamburger-button span {
    width: 2rem;
    height: 0.25rem;
    background:red;
    border-radius: 10px;
    transition: all 0.3s linear;
    position: relative;
    transform-origin: 1px;
  }
  
  .hamburger-button.open span:first-child {
    transform: rotate(45deg);
  }
  
  .hamburger-button.open span:nth-child(2) {
    opacity: 0;
  }
  
  .hamburger-button.open span:nth-child(3) {
    transform: rotate(-45deg);
  }
  
  .menu {
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 300px;
    background-color: #f1f1f1;
    transform: translateX(-100%);
    transition: transform 0.3s ease-in-out;
    z-index: 10;
  }
  
  .menu.open {
    transform: translateX(0);
  }
  
  .menu ul {
    list-style: none;
    padding: 0;
    margin: 0;
    flex-direction: column;
  }
  
  .menu li {
    padding: 1rem;
  }
  
  .menu a {
    text-decoration: none;
    color: #333;
    font-size: 1.2rem;
  }

  .close-button {
    position: absolute;
    top: 10px;
    right: 10px;
    background: none;
    border: none;
    font-size: 24px;
    cursor: pointer;
    color: #333; /* Ajustez la couleur selon votre design */
  }

  .close-button:hover {
    color: #000; /* Couleur au survol */
  }