.navbar {
    background-color: #333;
    width: 100%;
    padding: 1rem 0;
    position: sticky;
    top: 0;
    z-index: 9;
  }
  
  .navbar ul {
    display: flex;
    justify-content: center;
    list-style-type: none;
    width: 80%;
  }
  
  .navbar li {
    margin-left: 2rem;
  }
  
  .navbar a {
    color: #fff;
    text-decoration: none;
    font-weight: 500;
    transition: color 0.3s ease;
  }
  
  .navbar a:hover {
    color: #ffd700;
  }